<template>
  <div class="home">
    <otherLayoutVue :bannerUrl="'/banner/caseBanner.png'" :title="''" :discription="' '">
      <template #container>
        <detailLayoutVue
          :backTitle="'INVESTMENT CASE'"
          :mainTitle="''"
          :boxWidth="'width:700rem'"
          :isShowContent="false"
        ></detailLayoutVue>

        <div class="case-content-wrapper">
          <div class="case-content-nav showWH">
            <div
              class="nav-item"
              @click="choiceItem(i)"
              :class="choiceID === i ? 'active' : ''"
              v-for="(n, i) in classify"
              :key="n"
            >
              {{ n }}
            </div>
          </div>
          <div class="case-content fadeContent">
            <div class="case-content-item" v-for="(n,i) in content" :key="n.name">
              <div class="case-content-img-box">
                <img :src="n.src" :alt="n.name" />
              </div>
              <div class="case-content-name">{{ n.name }}</div>
              <div class="case-detail-wrap">
                <div class="case-detail-title">{{ n.name }}</div>
                <div class="case-detail-tiao"></div>
                <div class="case-detail-content">{{ n.discription }}</div>
                <div class="view-all" @click="handleDetail(i)">
                  <div>
                    <span class="arrow-icon"> </span>
                  </div>
                  <span class="view-text"></span>
                  <div class="line"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
    </otherLayoutVue>
  </div>
</template>
  
  <script>
import otherLayoutVue from "@/layout/otherLayout/otherLayout.vue";
import detailLayoutVue from "@/layout/otherLayout/detailLayout.vue";
// import DetailViewVue from '../components/DetailView/DetailView.vue';

