        },
        {
          name: "",
          discription:
            "",
          src: "/case/case1.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case2.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case3.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case4.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case5.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case6.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case7.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case8.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case9.png",
        },
        {
          name: "",
          discription:
            "",
          src: "/case/case10.png",
        },
      ],
      choiceID: 0,
    };
  },
  methods: {
    choiceItem(msg) {
      this.choiceID = msg;
    },
    handleDetail(msg){
      // console.log("msg",msg)
      this.$router.push({
        name: "caseDetail",
        params:{
          detailId: msg
        }
      })
    }
