<style lang="less" scoped>
@import url(../styles/var.css);

.case-content-item:hover .case-detail-wrap{
  opacity: .95;
}

.case-content-item:hover .case-content-name{
  opacity: 0 !important;
}

.view-all {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 76rem;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding-bottom: 10rem;
  cursor: pointer;
  overflow: hidden;
  margin-left: 188rem;
  margin-top:18rem;
}

.line {
  position: absolute;
  height: 1rem;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #fff;
  opacity: 0.7;
}

.view-text {
