  font-size: 12rem;
  line-height: 16rem;
  color: #fff;
}
// ../../assets/icon_rt.png
.arrow-icon {
  display: inline-block;
  background-image: url(../assets/icon_rt_w.png);
  width: 15rem;
  height: 9rem;
  color: #0065cc;
}



.case-detail-wrap {
  position: absolute;
  z-index: 21;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
