  opacity: 0;
  .case-detail-title {
    font-family: SourceHanSansCN-Regular;
    text-align: center;
    color: #f5f7f9;
    font-size: 18rem;
    height: 30rem;
    line-height: 30rem;
    margin-top: 21rem;
  }
  .case-detail-tiao {
    width: 74rem;
    height: 3rem;
    margin: 0 auto;
    background-color: #b97e4c;
  }
  .case-detail-content {
    padding-top: 18rem;
    width: 216rem;
    height: 54rem;
    overflow: hidden;
    margin: 0 auto;
    font-size: 12rem;
    text-align: center;
    color: #f5f7f9;
    font-family: SourceHanSansCN-Regular;
    opacity: 0.85;
  }
}

.case-content-wrapper {
  width: 100%;
  height: 827rem;
  background-color: #fff;
  margin-top: 46rem;
  position: relative;

  .case-content {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 85rem;
    width: 1200rem;
    // display: flex;
    // flex-wrap: wrap;
    // justify-content: space-between;
    // align-content: left;

    display: grid;
    grid-template-columns:repeat(4,1fr);
    .case-content-item {
      position: relative;
      margin-bottom: 60rem;
      margin-right: 20rem;
      cursor: pointer;
      height: 185rem;
    }
    .case-content-img-box {
      width: 280rem;
      height: 160rem;
      border-radius: 5rem;
      background-color: #f5f7f9;
      position: relative;
      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .case-content-name {
      position: absolute;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      z-index: 20;
      width: 260rem;
      height: 46rem;
      border-radius: 5rem;
      background-color: #ffffff;
      box-shadow: 0 0 24rem rgba(0, 0, 0, 0.1);
      text-align: center;
      line-height: 46rem;
      font-family: SourceHanSansCN-Regular;
      font-weight: Regular;
      color: #213244;
      font-size: 18rem;
      opacity: 1;
    }
  }

  .case-content-nav {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1200rem;
    background-color: #fff;
    padding: 0 214rem;
    height: 88rem;
    border-radius: 5rem;
    box-shadow: 0 0 24rem rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    .nav-item {
      position: relative;
      font-family: SourceHanSansCN-Bold;
      font-weight: Bold;
      text-align: center;
      color: #213244;
      font-size: 18rem;
      cursor: pointer;
    }
    .nav-item.active {
      color: #b97e4c;
    }

    .nav-item::after {
      position: absolute;
      left: 0;
      bottom: -32rem;
      content: "";
      width: 100%;
      height: 3rem;
      border-radius: 2rem;
      background-color: #b97e4c;
      transform: scale(0);
      transition: all 0.3s;
    }
    .active.nav-item::after {
      transform: scale(1);
    }

    .nav-item:hover::after {
      transform: scale(1);
    }
  }
}
</style>