<template>
  <div class="computedWrapper">
    <!-- <h1></h1>-->
    <!-- banner -->
    <section class="banner">
      <section class="card-wrapper">
        <div
          class="backgtoundImage"
          :style="`background-image: url(${bannerUrl})`"
        >
        <div class="bannerTitle" :class="isMove?'moveY1':''" @animationend="(isMove = false)">{{title}}</div>
        <div class="bannerDiscription" :class="isMove?'moveY2':''" @animationend="(isMove = false)">{{discription}}</div>

          <!-- <img src="https://www.zhiwintech.com/images/home/banners/banner3.png" > -->
          <!-- <div class="overlay"></div> -->
        </div>
      </section>
    </section>
    <slot name="container">
        
    </slot>
  </div>
</template>
<script>
    export default {
        props:{
            bannerUrl:{
                type: String,
                require:true
            },
            title:{
              type:String,
              require:true
            },
            discription:{
              type: String,
              require: true
            }
        },
        data(){
          return {
            isMove: false
          }
        },
        mounted(){
          this.isMove = true
        },
        beforeDestroy(){
          this.isMove = false;
        }
    };
</script>
  <style lang="less" scoped>
  @keyframes down {
    0%{
      transform: translateY(-900rem);
    }
    90%{
      transform: translateY(20rem);
    }
    100%{
      transform: translateY(0rem);
    }
  }
  .moveY1{
    // animation: down 1s .2s;
    animation: down 1s .1s backwards;
    
  }
  .moveY2{
    animation: down .8s ;
  }
.banner {
  width: 100%;
  height: 399rem;
  .card-wrapper {
    width: 100%;
    height: 100%;
    .backgtoundImage {
      position: relative;
      width: 100%;
      height: 100%;
      // background: url(../assets/banner.png) ;
      background-repeat: no-repeat;
      background-position: center;
      object-fit: cover;
      .bannerTitle{
        position: absolute;
        z-index:22;
        left: 369rem;

        top: 194rem;
        font-family: SourceHanSansCN-Bold;
        font-weight: bold;
        color: #fff;
        font-size: 42rem;
      }
      .bannerDiscription{
        position:absolute;
        left: 370rem;
        top: 268rem;
        font-family: SourceHanSansCN-Regular;
        font-size: 24rem;
        letter-spacing: 5rem;
        color: #fff;
      }
    }
    
  }
}
</style>