<template>
  <div class="page-center" >
    <!-- <span @click="handleChange(1)" :class="{ disabled: current === 1 }"></span> -->
    <span @click="clickChange(current-1)" class="lastPage" :class="{ disabled: current === 1 }"><</span>
    <!-- <span :class="{active: current ===1}">1</span> -->
    <span 
    class="pageStyle"
    @click="handleChange(n)" 
    v-for="(n, i) in numbers" 
    :key="i" 
    :class="{ active: n === current }"
      >{{n}}</span
    >
    <span @click="clickChange(current+1)" class="nextPage" :class="{ disabled: current === lastPage }">></span>
    <!-- <span @click="handleChange(lastPage)" :class="{ disabled: current === lastPage }"></span> -->
  </div>
</template>

<script>
export default {
  props: {
    current: {
      type: Number,
    },
    allContent: {
      type: Number,
    },
    siglePageContent: {
      type: Number,
    },
    pageNumber: {
      type: Number,
    },
  },
  computed: {
    // 
    lastPage() {
      return Math.ceil(this.allContent / this.siglePageContent);
    },
    // 
    minPageIndex() {
      let min = Math.floor(this.current - Math.floor(this.pageNumber / 2));
      if(min<1){
        min = 1
      }
      return min
    },
    // 
    maxPageIndex() {
      let max = this.pageNumber + this.minPageIndex - 1;
      if(max > this.lastPage){
        max = this.lastPage
      }
      
      return max
    },
    // 
    numbers() {
      let nums = [];
      for (let i = this.minPageIndex; i <= this.maxPageIndex; i++) {
        nums.push(i);
      }
      return nums;
    },
  },
  methods:{
    handleChange(i){
       this.$emit("getHandel", i);  //
    },
    clickChange(i){
       if(this.current === 1 || this.current === this.lastPage) return 
       this.$emit("getHandel", i);  //
    }
  }
};
</script>

<style lang = "less" scoped>
@import "@/styles/var.less";
.lastPage,.nextPage{
  width: 46rem;
  height: 46rem;
  border-radius: 50%;
  border: 2rem solid #E1B792;
  color: #E1B792;
  background-color: #fff;
  text-align: center;
  line-height: 46rem;
  margin-left: 24rem;

  
}



.pageStyle {
  width: 46rem;
  height: 46rem;
  line-height: 46rem;
  text-align: center;
  color: #B97E4C;
  background-color: #fff;
  cursor: pointer;
  border-radius: 50%;
  margin-left: 24rem;
  border: 2rem solid #E1E4E8;
  
  
}

.active {
    color: #fff;
    background-color: #B97E4C;
    cursor: text;
    
  }

  .disabled {
    color: @gray;
    border-color: @gray;
    cursor: not-allowed !important;
  }
.page-center {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
</style>