<template>
  <div class="newsListWrap">
    <otherLayoutVue :bannerUrl="'/banner/newsListBanner.png'" :title="''" :discription="' '">
      <template #container>
        <detailLayoutVue
          :backTitle="'NEWS INFORMATION'"
          :mainTitle="''"
          :boxWidth="'width:800rem'"
          :isShowContent="false"
        ></detailLayoutVue>

        <div class="newsList-content-wrapper fideUp">
          <div class="newslist-content-item" @click = "getNewsDtail(i)" v-for="(n, i) in newsLst" :key="i">
            <div
              class="newslist-content-pic"
              :style="`background-image:url(${n.src})`"
            ></div>
            <div class="newslist-content-msg">
              <div class="newslist-content-title">{{ n.title }}</div>
              <div class="newslist-content-time">{{ n.time }}</div>
              <div class="newslist-content">{{ n.content }}</div>
              <div class="newslist-content-detailView">
                <DetailViewVue></DetailViewVue>
              </div>
            </div>
          </div>
        </div>
        <div class="page-wrap fideUp">
          <!--  -->
          <getPage
            :current="currentId"
            :allContent="6"
            :siglePageContent="3"
            :pageNumber="6"
            @getHandel="changeHandel($event)"
          ></getPage>
        </div>
      </template>
    </otherLayoutVue>
  </div>
</template>

<script>
import otherLayoutVue from "@/layout/otherLayout/otherLayout.vue";
import detailLayoutVue from "@/layout/otherLayout/detailLayout.vue";
import DetailViewVue from "../components/DetailView/DetailView.vue";
import getPage from "@/getPage";
export default {
  components: {
    detailLayoutVue,
    otherLayoutVue,
    DetailViewVue,
    getPage,
  },
  data() {
    return {
      newsLst: [
        {
          title: "B",
          time: "2022-11-08",
          content:
            "B  ",
          src: "/newslist/new.png",
        },
        {
          title: "B",
          time: "2022-11-08",
          content:
            "B  ",
          src: "/newslist/new.png",
        },
        {
          title: "B",
          time: "2022-11-08",
          content:
            "B  ",
          src: "/newslist/new.png",
        },
      ],
      currentId: 1
    };
  },

  methods: {
    changeHandel(i) {
        console.log(i)
      let query = {
        page: i,
      };
      //categoryId-1
          this.$router.push({
            name: "newsPaging",
            params: { pageId: i },
            query,
          });

          this.currentId = i
        
    },
    getNewsDtail(i){
        // console.log("",i)
        this.$router.push({
            name: "newsDetail",
            params: { newId: i },

        })
    }
  },
};
</script>

<style lang="less" scoped>
@keyframes fadeUp {
  0%{
    transform: translateY(50rem);
    opacity: 0;
  }
  100%{
    transform: translateY(0);
    opacity: 1;
  }
}

.fideUp{
  animation: fadeUp .3s ;
}

.page-wrap{
    background-color: #fff;
    padding-bottom: 50rem;
}
.newsList-content-wrapper {
  width: 100%;
  padding: 60rem 370rem;
  background-color: #fff;
  .newslist-content-item {
    width: 1132rem;
    height: 296rem;
    background-color: #fff;
    border: 10rem solid #e1b792;
    box-shadow: 0 0 20rem rgba(0, 0, 0, 0.15);
    margin-bottom: 63rem;
    position: relative;
    cursor: pointer;
    .newslist-content-pic {
      width: 311rem;
      height: 310rem;
      background-repeat: no-repeat;
      background-position: center;
      object-fit: cover;
      position: absolute;
      top: -17rem;
      left: 28rem;
      background-size: 100% 100%;
    }
    .newslist-content-msg {
      position: absolute;
      left: 380rem;
      padding: 36rem 35rem 36rem 0;
      width: calc(100% - 380rem);

      .newslist-content-title {
        font-family: SourceHanSansCN-Bold;
        font-weight: Bold;
        text-align: left;
        color: #1f1f1f;
        font-size: 24rem;
        line-height: 28rem;
        letter-spacing: 5rem;
      }
      .newslist-content-time {
        font-family: SourceHanSansCN-Bold;
        font-weight: bold;
        text-align: left;
        color: #1f1f1f;
        line-height: 28rem;
        letter-spacing: 2rem;
        font-size: 14rem;
      }
      .newslist-content {
        font-family: SourceHanSansCN-Normal;
        text-align: left;
        color: #1f1f1f;
        line-height: 28rem;
        font-size: 12rem;
        letter-spacing: 3rem;
      }
      .newslist-content-detailView {
        display: flex;
        justify-content: end;
        margin-top: 37rem;
      }
    }
  }

  .newslist-content-item:hover {
    border-color: #911826 !important;
  }
  .newslist-content-item:hover .newslist-content-pic {
    transform: scale(1.1) !important;
  }
}
</style>